@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: 'FontAwesome';
  font-display: swap;
  src: url("../fonts/fontawesome-webfont.woff") format("woff"); }

.fas {
  font-family: 'FontAwesome';
  font-weight: 900;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1; }

.fa-check:before {
  content: "\f00c"; }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.container {
  width: 100%;
  max-width: 1680px;
  padding: 10px 10px 0;
  margin: 0 auto; }
  @media screen and (max-width: 1680px) {
    .container {
      max-width: 1400px; } }
  @media screen and (max-width: 1490px) {
    .container {
      max-width: 1240px; } }
  @media screen and (max-width: 1340px) {
    .container {
      max-width: 1140px; } }
  @media screen and (max-width: 1220px) {
    .container {
      max-width: 960px; } }
  @media screen and (max-width: 992px) {
    .container {
      max-width: 730px; } }
  @media screen and (max-width: 767px) {
    .container {
      max-width: 100%; } }

.sections {
  margin-bottom: 20px; }
  .sections__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap; }
  .sections__img {
    height: 15.625rem;
    margin-bottom: 0.625rem;
    display: flex;
    align-items: center;
    overflow: hidden;
    border-radius: 18px; }
    @media (hover: hover), screen and (min-width: 0\0) {
      .sections__img:hover img {
        transform: scale(1.1); } }
    @media (max-width: 1490px) {
      .sections__img {
        height: 14.0625rem; } }
    @media (max-width: 1340px) {
      .sections__img {
        height: 12.5rem; } }
    @media (max-width: 1220px) {
      .sections__img {
        height: 10.9375rem; } }
    @media (max-width: 767px) {
      .sections__img {
        height: auto; } }
    .sections__img img {
      border-radius: inherit;
      object-fit: cover;
      transition: transform 0.5s ease;
      height: 100%; }
  .sections__elem {
    width: 25%;
    padding: 0 1.25rem;
    margin-bottom: 1.25rem; }
    @media (max-width: 1680px) {
      .sections__elem {
        padding: 0 0.625rem; } }
    @media (max-width: 1220px) {
      .sections__elem {
        width: 33%; } }
    @media (max-width: 767px) {
      .sections__elem {
        width: 50%; } }
    @media (max-width: 580px) {
      .sections__elem {
        width: 100%; } }
  .sections__text {
