    @media (max-width: 767px) {
      .sections__text {
        font-size: 0.75rem; } }
  @media (max-width: 767px) {
    .sections__number {
      font-size: 0.75rem; } }
  .sections__info {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .sections__title {
    text-align: center;
    margin-right: 0.25rem;
    text-transform: uppercase;
    color: #1a3c55;
    font-size: 1.2rem; }
    @media (max-width: 1220px) {
