@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: 'FontAwesome';

  font-display: swap;
  src: url("../fonts/fontawesome-webfont.woff") format("woff");
}

.fas {
  font-family: 'FontAwesome';
  font-weight: 900;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
}

.fa-check:before {
  content: "\f00c";
}
